import { Link } from 'gatsby';
import React, { useEffect, useState } from 'react';


export const sections = [
  { id: 'post', label: 'Gii Php' },
  { id: 'work', label: 'Hnh Trnh KH' },
  { id: 'service', label: 'Tnh Nng' },
  { id: 'industry', label: 'ng dng' },
  { id: 'system', label: 'L trnh' },
  { id: 'tes', label: 'Khch Hng' },
  { id: 'contact', label: 'Lin H' },
];

const NavMenu = () => {
  const [activeId, setActiveId] = useState<string>('');

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY + 100;
      let newActiveId = '';

      for (const section of sections) {
        const element = document.getElementById(section.id);
        if (element) {
          const offsetTop = element.offsetTop;
          const offsetHeight = element.offsetHeight;

          if (scrollPosition >= offsetTop && scrollPosition < offsetTop + offsetHeight) {
            newActiveId = section.id;
            break;
          }
        }
      }
      setActiveId(newActiveId);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      <ul id="list-example">
        {sections.map(({ id, label }) => (
          <li key={id}>
            <Link
              className={`list-group-item list-group-item-action ${activeId === id ? 'active' : ''}`}
              to={`#${id}`}
            >
              {label}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
};

export default NavMenu;
