import { useEffect, useRef } from 'react';
import HeaderLogo from '@/assets/img/logo/header-logo2.png';
import HeaderLogo1 from '@/assets/img/logo/header-logo1.png';
import { Col, Container } from 'react-bootstrap';
import { FaBarsStaggered, FaXmark } from 'react-icons/fa6';
import useToggle from '@/hooks/useToggle';
import { Link } from 'gatsby';
import React from 'react';
import { sections } from './NavMenu';

const MobileTop = () => {
  const { isOpen, toggle } = useToggle();
  const sidebarRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (isOpen && sidebarRef.current && !sidebarRef.current.contains(event.target as Node)) {
        toggle();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [isOpen, toggle]);

  return (
    <div>
      <div className="mobile-header mobile-header3 d-block d-lg-none ">
        <Container fluid>
          <Col xs={12}>
            <div className="mobile-header-elements">
              <div className="mobile-logo">
                <Link to="/multi/home-1">
                  <img src={HeaderLogo} alt="" />
                </Link>
              </div>
              <div onClick={toggle} className="mobile-nav-icon">
                <FaBarsStaggered />
              </div>
            </div>
          </Col>
        </Container>
      </div>

      <div
        ref={sidebarRef}
        className={`mobile-sidebar mobile-sidebar4  d-block d-lg-none ${isOpen ? 'mobile-menu-active' : ''}`}
      >
        <div className="logo-m">
          <Link to="/">
            <img src={HeaderLogo1} alt="" />
          </Link>
        </div>
        <div onClick={toggle} className="menu-close">
          <FaXmark />
        </div>
        <div className="mobile-nav">
          <ul id="list-example">
            {sections.map(({ id, label }) => (
              <li key={id}>
                <Link className="list-group-item list-group-item-action" to={`#${id}`} onClick={toggle}>
                  {label}
                </Link>
              </li>
            ))}
          </ul>
          <a className="sidebar1-btn" href="#cdp-form-modal" onClick={toggle}>
            <span>T Vn Ngay</span>
          </a>
        </div>
      </div>
    </div>
  );
};

export default MobileTop;
