type ScrollTabsProps = {
  tabs: Stage[];
  activeTab: Stage;
  onChange: (key: Stage) => void;
  children?: React.ReactNode;
};

const ScrollTabs = ({ tabs, activeTab, onChange, children }: ScrollTabsProps) => {
  const ref = useRef<HTMLDivElement | null>(null);
  const accRef = useRef(0);
  const dirRef = useRef<'up' | 'down' | null>(null);
  const cooldownRef = useRef(false);
  useEffect(() => {
    const handler = (e: WheelEvent) => {
      const el = ref.current;
      if (!el) return;
      const rect = el.getBoundingClientRect();
      const inView = rect.top < window.innerHeight && rect.bottom > 0;
      if (!inView) return;
      const idx = tabs.findIndex(t => t === activeTab);
      const direction = e.deltaY > 0 ? 'down' : e.deltaY < 0 ? 'up' : null;
      if (!direction) return;
      const THRESHOLD = 200;
      if (dirRef.current && dirRef.current !== direction) {
        accRef.current = 0;
      }
      dirRef.current = direction;
      const canScrollTabs =
        (direction === 'down' && idx < tabs.length - 1) ||
        (direction === 'up' && idx > 0);
      if (canScrollTabs) {
        e.preventDefault();
        accRef.current += Math.abs(e.deltaY);
        if (accRef.current >= THRESHOLD && !cooldownRef.current) {
          const nextIdx = direction === 'down' ? idx + 1 : idx - 1;
          onChange(tabs[nextIdx]);
          accRef.current = 0;
          cooldownRef.current = true;
          setTimeout(() => { cooldownRef.current = false; }, 900);
        }
      } else {
        accRef.current = 0;
      }
    };
    const el = ref.current;
    if (el) el.addEventListener('wheel', handler, { passive: false });
    return () => { if (el) el.removeEventListener('wheel', handler as any); };
  }, [activeTab, tabs, onChange]);
  return <div ref={ref}>{children}</div>;
};
